import React, { useState, useEffect } from "react";
import styled from "styled-components";

import img from "../../assets/cutsvg/r5.svg";

const Continer = styled.div`
  background-image: linear-gradient(216deg, #1e87ff 42%, #001f77 100%);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
`;
const BackgroundImageDown = styled.img`
  width: 100%;
  position: absolute;
  left: 0;
  z-index: 0;
  bottom: 0;
`;
const BackgroundImageUp = styled.img`
  -webkit-transform: rotate(180deg); /* Chrome and other webkit browsers */
  -moz-transform: rotate(180deg); /* FF */
  -o-transform: rotate(180deg); /* Opera */
  -ms-transform: rotate(180deg); /* IE9 */
  transform: rotate(180deg);
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
`;
const ShopWay = () => {
  const [current, setCurrent] = useState(0);
  useEffect(() => {
    setTimeout(() => {
      if (current === 4) {
        setCurrent(0);
      } else {
        setCurrent(current + 1);
      }
    }, 7000);
  }, [current]);

  const currentClass = (step) => {
    if (step < current) {
      return "stepper__list__item--done";
    } else if (step === current) {
      return "stepper__list__item--current";
    } else {
      return "stepper__list__item--pending";
    }
  };
  const itDone = () => (
    <svg class="stepper__list__icon" viewbox="0 0 24 24">
      <path
        class="st0"
        d="M11.1 12.9l-1.2-1.1c-.4-.3-.9-.3-1.3 0-.3.3-.4.8-.1 1.1l.1.1 1.8 1.6c.1.1.4.3.7.3.2 0 .5-.1.7-.3l3.6-4.1c.3-.3.4-.8.1-1.1l-.1-.1c-.4-.3-1-.3-1.3 0l-3 3.6z"
      ></path>
      <path
        class="st0"
        d="M12 20c4.4 0 8-3.6 8-8s-3.6-8-8-8-8 3.6-8 8 3.6 8 8 8zm0 1.5c-5.2 0-9.5-4.3-9.5-9.5S6.8 2.5 12 2.5s9.5 4.3 9.5 9.5-4.3 9.5-9.5 9.5z"
      ></path>
    </svg>
  );

  const noteDone = () => (
    <svg
      class="stepper__list__icon stepper__list__icon--current"
      width="24"
      height="24"
      viewbox="0 0 24 24"
    >
      <path d="M12 16.1c1.8 0 3.3-1.4 3.3-3.2 0-1.8-1.5-3.2-3.3-3.2s-3.3 1.4-3.3 3.2c0 1.7 1.5 3.2 3.3 3.2zm0 1.7c-2.8 0-5-2.2-5-4.9S9.2 8 12 8s5 2.2 5 4.9-2.2 4.9-5 4.9z"></path>
    </svg>
  );
  const pending = () => (
    <svg
      class={"stepper__list__icon stepper__list__icon--current"}
      width="24"
      height="24"
      viewbox="0 0 24 24"
    >
      <path
        d="M12.2 20a8 8 0 1 0 0-16 8 8 0 0 0 0 16zm0 1.377a9.377 9.377 0 1 1 0-18.754 9.377 9.377 0 0 1 0 18.754zm-4-8a1.377 1.377 0 1 1 0-2.754 1.377 1.377 0 0 1 0 2.754zm4 0a1.377 1.377 0 1 1 0-2.754 1.377 1.377 0 0 1 0 2.754zm4 0a1.377 1.377 0 1 1 0-2.754 1.377 1.377 0 0 1 0 2.754z"
        fill="#006DFF"
        fill-rule="evenodd"
      ></path>
    </svg>
  );
  const svg = (step) => {
    if (step < current) {
      return itDone();
    } else if (step === current) {
      return pending();
    } else {
      return noteDone();
    }
  };

  const getStep = (step) => {
    switch (step) {
      case 0:
        return {
          title: "  فروشگاه ",
          decription:
            "با رفتن به فروشگاه ایران تک سایز متونید از کلی کالا  بهترینشو برای خودتون انتخاب کنید",
        };
      case 1:
        return {
          title: " انتخاب کالا",
          decription:
            "کالای مورد نظرتونو از توی دسته بندی ها بگردین و پیدا کنید",
        };
      case 2:
        return {
          title: " استعلام نهایی از فروشنده ",
          decription:
            "وقتی کالاتونو پیدا کردین مطمعن بشین که کالاتون هنوز موجوده قبل از هر پرداختی ",
        };
      case 3:
        return {
          title: " تکمیل فرایند خرید ",
          decription:
            " فروشنده بعد از استعلام یه زمانی رو تعیین میکنه که اون کالا رو براتون نگه داره پس هرچی سریعتر فرایند خریدتونو تکمیل کنید چون بعد از تموم شدن اعتبار استعلام فروشنده و ما هیچ مسولیتی واسه رزرو کالاتون نخواهیم داشت",
        };
      case 4:
        return {
          title: "انتخاب روش پرداخت",
          decription:
            "از بین همه روش های پرداخت میتونید یکی رو انتخاب کنید اگه پرداخت در محل رو انتخاب کردین برای مطمعن شدن فروشنده یه مبلغیاز کیف پولتون پیش ما امانت میمونه",
        };
      case 5:
        return {
          title: "انتخاب روش ارسال",
          decription:
            "یه روش راه مطمعن رو از بین روش های پیشنهادیه ما انتخاب کنید",
        };
      case 6:
        return {
          title: "دریافت",
          decription: "مبارکتون باشه",
        };

      default:
        break;
    }
  };
  return (
    <Continer>
      <div class="wrapper">
        <BackgroundImageUp src={img} alt="bg-svg" />
        <BackgroundImageDown src={img} alt="bg-svg" />
        <nav class="stepper">
          <ul class="stepper__list">
            <li class={`stepper__list__item ${currentClass(0)}`}>
              {svg(0)}
              <span class="stepper__list__title"> {getStep(0).title} </span>
            </li>
            <li class={`stepper__list__item ${currentClass(1)}`}>
              {svg(1)}
              <span class="stepper__list__title">{getStep(1).title}</span>
            </li>
            <li class={`stepper__list__item ${currentClass(2)}`}>
              {svg(2)}
              <span class="stepper__list__title">{getStep(2).title}</span>
            </li>
            <li class={`stepper__list__item ${currentClass(3)}`}>
              {svg(3)}
              <span class="stepper__list__title">{getStep(3).title}</span>
            </li>
            <li class={`stepper__list__item ${currentClass(4)}`}>
              {svg(4)}
              <span class="stepper__list__title">{getStep(4).title}</span>
            </li>
            <li class={`stepper__list__item ${currentClass(5)}`}>
              {svg(4)}
              <span class="stepper__list__title">{getStep(5).title}</span>
            </li>{" "}
            <li class={`stepper__list__item ${currentClass(6)}`}>
              {svg(4)}
              <span class="stepper__list__title">{getStep(6).title}</span>
            </li>
          </ul>
          <Decription
            title={getStep(current).title}
            decription={getStep(current).decription}
          />
        </nav>
      </div>
    </Continer>
  );
};

const Decription = (props) => {
  const [state, setstate] = useState({
    title: props.title,
    decription: props.decription,
  });

  useEffect(() => {
    setstate({ title: props.title, decription: props.decription });
  }, [props]);

  return (
    <div className="decription ">
      <h1 className="decriptionH1">{state.title}</h1>
      <h3 className="decriptionH3">{state.decription}</h3>
    </div>
  );
};

export default ShopWay;
